<template>
    <v-app id="inspire">
        <v-navigation-drawer
            fixed
            v-model="drawer"
            app
            dark
            floating
            persistent
            mobile-break-point="991"
            width="260"
            :style="{'background-image': `url(${sideimage})`, 'background-position': 'center center', 'background-size': 'cover'}"
        >
            <v-list dense style="background: rgba(27,27,27,.74);height:100%;overflow:auto;">
                <v-list-tile avatar style="padding: 15px 0;" exact to="/blog">
                    <v-list-tile-avatar color="white">
                        <v-img :src="authorimage" height="34" contain/>
                    </v-list-tile-avatar>
                    <v-list-tile-title class="title">profos</v-list-tile-title>
                </v-list-tile>
                <v-divider/>
                <template v-for="item in items">
                    <v-list-group
                        v-if="item.children"
                        v-model="item.model"
                        :key="item.text"
                    >
                        <v-list-tile slot="activator">
                            <v-list-tile-content>
                                <v-list-tile-title>{{ item.text }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile active-class="grey lighten-1" exact :to="child.url" v-for="(child, i) in item.children" :key="i">
                            <v-list-tile-content>
                                <v-list-tile-title>{{ child.text }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </v-list-group>
                    <v-list-tile exact :to="item.url" v-else :key="item.text">
                        <v-list-tile-content>
                            <v-list-tile-title>{{ item.text }}</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </template>
            </v-list>
        </v-navigation-drawer>
        <v-toolbar app flat prominent style="background: #eee;">
            <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
                <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <iframe src="https://ghbtns.com/github-btn.html?user=qzruncode&repo=blog&type=star&count=true&size=large" frameborder="0" scrolling="0" width="160px" height="30px"></iframe>
        </v-toolbar>
        <v-content style="background: #eee;">
            <v-container fluid fill-height style="position:relative;">
                <v-layout justify-center align-center>
                    <transition name="fade" mode="out-in">
                        <router-view></router-view>
                    </transition>
                </v-layout>
            </v-container>
        </v-content>
        <v-footer class="pa-3">
            <v-spacer></v-spacer>
            <div>Copyright &copy; {{ new Date().getFullYear() }} 叶家伟</div>
        </v-footer>
    </v-app>
</template>

<script>

import sideimage from "./assets/aside.jpg";
import authorimage from "./assets/author.jpg";
export default {
    name: "App",
    props: {
        source: String
    },
    data() {
        return {
            drawer: null,
            sideimage: sideimage,
            authorimage: authorimage,
            items: [
                {
                    text: "Html",
                    model: false,
                    children: [
                        { text: "构建map映射图片", url: "/blog/html/map" },
                    ]
                }
            ]
        };
    }
};
</script>

<style lang="scss">
.v-navigation-drawer > .v-list .v-list__tile {
    font-size: 14px !important;
    font-weight: 300;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>

